<template>
  <div class="avatar-layout">
    <header class="avatar-header">
      <a onclick="window.history.go(-1);" class="avatar-back-btn"></a>
      <h2 class="avatar-title"></h2>
    </header>
    <section class="avatar-main">
      <div class="avatar-img">
        <input type="file" accept="image/jpeg,image/jpg,image/png" capture="camera" @change="onFileChange"  style="display: none;">
        <ul class="list-ul" v-show="!isPhoto">
          <li class="list-li">
              <img :src="imgUrls" class="avatar-i">
          </li>
        </ul>
      </div>
      <div class="avatar-choice">
        <button class="choice-xiangce" @click="addPic()"></button>
      </div>
    </section>
  </div>
</template>
<script>
  //
  import Api from '../../../api/api';
  //
  import lrz from 'lrz';
  export default {
    data: function () {
      return {
        imgUrls: '',
        urlArr: [],
        isPhoto: true,
        btnTitle: '',
        isModify: false,
        previewImg:'',
        isPreview: false
      }
    },
    mounted(){
        const temAvatar = localStorage.getItem('avatar');
        if(temAvatar!==null){
          this.imgUrls = temAvatar;
        }
    },
    watch: {
      imgUrls: 'toggleAddPic'
    },
    methods: {
      showMsg(str){
        this.$toast({
          message: str,
          duration: 2000,
        });
      },
      toggleAddPic(){
        let vm = this;
        if(vm.imgUrls!==null || vm.imgUrls!==''){
          if(vm.imgUrls.length >= 1) {
            vm.isPhoto = false;
          } else {
            vm.isPhoto = true;
          }
        }

      },
      /**
       *  
       * */
      addPic(){
        $('input[type=file]').trigger('click');
        return false;
      },
      /**
       * ,
       * */
      onFileChange(e){
        const files = e.target.files || e.dataTransfer.files;
        if(!files.length) return;
        this.createImage(files, e);
      },
      /***
       * 
       * */
      createImage(file,e){
        let vm = this;
        /**
         * rst:
         * */
        lrz(file[0], { width: 750 }).then(function(rst) {
          vm.imgUrls = rst.base64;
          localStorage.removeItem('avatar');
          localStorage.setItem('user-avatar',vm.imgUrls);
          history.back(-1);
          return rst;
        }).always(function() {
          // 
          e.target.value = null;
        });
      },
      /**
       * 
       * */
      /*saveImage: function(imgUrls){
        const _this = this;
          //,
        console.log("base64:",imgUrls);
        /!*this.initAxios().post(Api.UPLOADAVATORINFOS,{

        }).then((res)=>{
            _this.showUpLoadImgInfo(res);
          },(err)=>{
            _this.showUpLoadImgWarning();
             console.log(":",err);
          }
        );*!/

      },*/
    }
  }
</script>
<style scoped>
  @import '../../../../src/assets/css/avatar.css';
</style>



